<template>
  <div id="personal-data">
    <head-top title="个人资料"></head-top>
    <div class="form">
      <div class="form-item" v-for="(item, i) in personDatas" :key="i">
        <div class="fL">{{item.info}}</div>
        <div class="fR">{{item.value}}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},
  props: {},
  data () {
    return {
      personDatas: []
    };
  },
  computed: {},
  watch: {},
  created () {
    if (this.$store.getters.teacher) {
      const teacher = this.$store.getters.teacher;
      this.personDatas = [
        { info: "姓名", value: teacher["TeacherName"] },
        { info: "性别", value: teacher["TeacherGender"] === "1" ? "男" : "女" },
        { info: "教工号", value: teacher["TeacherNo"] },
        { info: "卡编号", value: teacher["TeacherCardNo"] }
      ];
    } else {
      this.personDatas = [
        { info: "姓名", value: "" },
        { info: "性别", value: "男" },
        { info: "教工号", value: "" },
        { info: "卡编号", value: "" }
      ];
    }
  }
};
</script>

<style lang='less'>
@import "./../../styles/common/base";
#personal-data {
  .form {
    width: 100%;
    padding-bottom: 2vh;
    background: rgba(255, 255, 255, 1);
  }

  .fL {
    font-size: @font-info;
    color: #333;
  }

  .fR {
    font-size: @font-info;
    color: #999;
  }
  .form-item {
    height: 8vh;
    width: 92vw;
    margin: 0 auto;
    line-height: 8vh;
    border-bottom: 1px solid rgba(221, 221, 221, 1);
  }
}
</style>
